@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

*{
    margin: 0;
    padding: 0;
}

body{
    background: #fff;
    color: #222;
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
}

.wrapper{
    width: 1280px;
    margin: 50px auto;
}

header{
    position: relative;
}

header .logo{
    font-weight: 600;
    font-size: 20px;
}

header ul.nav{
    float: right;
    list-style: none;
}

header ul.nav li{
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms;
}

header ul.nav li:hover{
    opacity: 0.5;
}

header .presentation{
    margin: 50px 0;
    background: url("./img/bg.jpg") no-repeat;
    width: 100%;
    height: 500px;
    background-position: center;
    background-size: cover;
    background-blend-mode: multiply;
    background-color: #bcbcbc;
    position: relative;
}

header .presentation::after{
    content: "The Best Furniture For Your House";
    position: absolute;
    color: #fff;
    top: 100px;
    left: 50px;
    width: 300px;
    font-size: 40px;
    font-weight: 800;
}

header .presentation::before{
    content: "at low prices";
    position: absolute;
    color: #fff;
    top: 250px;
    left: 50px;
    width: 300px;
    font-size: 15px;
    font-weight: 400;
}


header .shop-cart-button{
    float: right;
    cursor: pointer;
    transition: color 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button.active{
    color: #dc3d3d;
}

header .shop-cart{
    position: absolute;
    top: 30px;
    right: 0px;
    width: 400px;
    background: #fafafa;
    box-shadow: 7px 10px 26px -3px rgba(0,0,0,0.75);
    -webkit-box-shadow: 7px 10px 26px -3px rgba(0,0,0,0.75);
    -moz-box-shadow: 7px 10px 26px -3px rgba(0,0,0,0.75);
    z-index: 1000;

    padding: 20px;
    padding-bottom: 0;
}

header .empty{
    text-align: center;
}

header .shop-cart .item{
    width: 100%;
    float: left;
    margin-bottom: 20px;
}

header .shop-cart .item img{
    width: 70px;
    float: left;
    margin-right: 20px;
}

header .shop-cart .item h2{
    font-size: 20px;
    margin-bottom: 10px;
}

header .shop-cart .item p{
    color: #797979;
    font-weight: 600;
}


main{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item{
    width: 30%;
    margin-bottom: 50px;
    background: #f5f5f5ff;
    position: relative;
    padding-bottom: 20px;
}

main .item img{
    width: 100%;
    border-radius: 10px 10px 0px 0px;
}

main .item img:hover{
    transform: scale(1.05);
}

main h2, main p, main b{
    margin: 10px 20px;
    color: #333;
}

main b{
    color: #5fa36a;
}

main .add-to-cart{
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    transition: transform 500ms;
}

main .add-to-cart:hover{
    transform: scale(1.5) translateY(-5px);
}

footer{
    text-align: center;
    margin-top: 100px;
}

